<template>
  <div class="content">
    <h2>Dropdown <span></span></h2>
    <div class="demo">
      <h3><span></span></h3>
      <div class="test">
        <w-dropdown @change="onChange" icon="edit" :options="option1">
        default
        </w-dropdown>
        <w-dropdown @change="onChange" icon="edit" :options="option1" type="primary">
        primary
        </w-dropdown>
        <w-dropdown @change="onChange" icon="edit" :options="option1" type="success">
        success
        </w-dropdown>
        <w-dropdown @change="onChange" icon="edit" :options="option1" type="warning">
        warning
        </w-dropdown>
        <w-dropdown @change="onChange" icon="edit" :options="option1" type="danger">
        danger
        </w-dropdown>
        <w-dropdown @change="onChange" icon="edit" :options="option1" type="link">
        link
        </w-dropdown>
        <div class="hello-world"><em>{{data}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span></span></h3>
      <div class="test">
        <w-dropdown :options="option1" icon="edit" :disabled="true">
        default
        </w-dropdown>
        <w-dropdown :options="option1" icon="edit" type="primary" :disabled="true">
        primary
        </w-dropdown>
        <w-dropdown :options="option1" icon="edit" type="success" :disabled="true">
        success
        </w-dropdown>
        <w-dropdown :options="option1" icon="edit" type="warning" :disabled="true">
        warning
        </w-dropdown>
        <w-dropdown :options="option1" icon="edit" type="danger" :disabled="true">
        danger
        </w-dropdown>
        <w-dropdown :options="option1" icon="edit" type="link" :disabled="true">
        link
        </w-dropdown>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      data:'',
      option1: [{
        value: '0',
        label: '',
        selected:true
      }, {
        value: '1',
        label: '',
      }, {
        value: '2',
        label: '',
        disabled:true
      }, {
        value: '3',
        label: '',
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: '',
        line:true
      }]
    };
  },
  methods:{
    onChange(value,label){
      console.log(value)
      console.log(label)
      this.data = label;
      if (value==5) {
        this.test(value,label)
      }
    },
    test(value,label){
      this.data = ''+label
    }
  }
}
</script>


