<template>
  <div class="content">
    <h2>Notify <span></span></h2>
    <div class="demo">
      <h3><span></span></h3>
      <div class="test">
        <w-button @click="basic1">default</w-button>
        <w-button @click="primary" type="primary">primary</w-button>
        <w-button @click="success" type="success">success</w-button>
        <w-button @click="warning" type="warning">warning</w-button>
        <w-button @click="danger" type="danger">danger</w-button>
        <w-button @click="basic2">default</w-button>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      basic1() {
        this.$notify.open({
          content: '10',
          duration:10000,
          placement: 'top-left',
          icon: 'loading',
        });
      },
      basic2() {
        this.$notify.open({
          content: '10',
          duration:10000,
          placement: 'bottom-left',
        });
      },
      primary() {
        this.$notify.open({
          content: '', 
          type: 'primary',
          duration:10000,
        });
      },
      success() {
        this.$notify.success({
          content: '10',
          duration:10000,
          placement: 'top-right',
        });
      },
      warning() {
        this.$notify.warning({
          content: '10',
          duration:10000,
          placement: 'bottom-right',
        });
      },
      danger() {
        this.$notify.open({
          content: '10', 
          type: 'danger',
          duration:10000,
          placement: 'bottom-center',
        });
      },
    },
  };
</script>


