<template>
  <div class="content">
    <h2>DatePicker </h2>
    <div class="demo">
      <h3></h3>
      <div class="test">
        <w-datepicker id="a1" name="a1" placeholder="" class="w-160" v-model="date1"></w-datepicker>
        <div class="hello-world"><em>{{date1}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span></span></h3>
      <div class="test">
        <w-datepicker id="a2" name="a2" placeholder="" class="w-160" v-model="date2" :showWeek="true"></w-datepicker>
        <div class="hello-world"><em>{{date2}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span></span></h3>
      <div class="test">
        <w-datepicker placeholder="" class="w-160" v-model="date3" :clearable="false"></w-datepicker>
        <div class="hello-world"><em>{{date3}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span>{{min}}{{max}}</span></h3>
      <div class="test">
        <w-datepicker id="a" name="a" placeholder="" class="w-220" v-model="date5" :max="max" :min="min"></w-datepicker>
        <div class="hello-world"><em>{{date5}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span>{{min}}{{max}}</span></h3>
      <div class="test">
        <w-datepicker id="a" name="a" placeholder="" class="w-220" v-model="range1" :range="true" :max="max" :min="min"></w-datepicker>
        <div class="hello-world"><em>{{range1}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3></h3>
      <div class="test">
        <w-datepicker id="a5" name="a5" placeholder="" class="w-220" v-model="range" :range="true"></w-datepicker>
        <div class="hello-world"><em>{{range}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3></h3>
      <div class="test">
        <w-datepicker id="a4" name="a4" :disabled="true" placeholder="" class="w-160" v-model="date4"></w-datepicker>
        <div class="hello-world"><em>{{date4}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      date1: '',
      date2: '1984-02-13',
      date3: '2017-06-24',
      date4: '2012-02-26',
      date5: '2017-02-26',
      range: ['2011-10-03','2012-03-28'],
      range1: ['2017-05-03','2017-06-28'],
      max:'2017-08-28',
      min:'2016-08-28',
    }
  },
}
</script>
<style lang="scss">

</style>


