<template>
  <div class="content">
    <h2>Checkbox </h2>
    <div class="demo">
      <h3><span></span></h3>
      <div class="test">
        <w-checkbox name="test" v-model="checked" v-for="(name,index) in groupList" :key="name" :value="name">{{name}}</w-checkbox>
        <div class="hello-world"><em>{{checked}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span></span></h3>
      <div class="test">
        <w-checkbox type="block" v-model="all" @change.native='checkAll'></w-checkbox>
        <w-checkbox type="block" @change.native='check' name="test" :id="'checkbox'+index" v-model="checkboxList" v-for="(name,index) in groupList" :key="name" :value="name">{{name}}</w-checkbox>
        <div class="hello-world"><em>{{checkboxList}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3><span></span></h3>
      <div class="test">
        <w-checkbox :disabled="index==1||index==2" name="test" v-model="checkedDisable" v-for="(name,index) in groupList" :key="name" :value="name">{{name}}</w-checkbox>
        <div class="hello-world"><em>{{checkedDisable}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      all:false,
      checkboxList:[],
      checkboxValue:[],
      groupList: ['','','','',''],
      checked:[],
      checkedDisable:[''],
    };
  },
  methods:{
    check(){
      if (this.checkboxList.length === this.groupList.length) {
        this.all = true;
      }else{
        this.all = false;
      }
    },
    checkAll(){
      this.checkboxList = this.all ? this.groupList:[];
    }
  },
}
</script>


