<template>
  <div class="content">
    <h2>Select </h2>
    <div class="demo">
      <h3></h3>
      <div class="test">
        <w-select @change="onChange1" class="w-200" :options="option1" :title="data1" :value="data1">
        </w-select>
        <div class="hello-world"><em>{{data1}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3></h3>
      <div class="test">
        <w-select @change="onChange2" class="w-400" :options="option2" :multiple="true" :title="data2" :value="data2">
        </w-select>
        <div class="hello-world"><em>{{data2}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
    <div class="demo">
      <h3></h3>
      <div class="test">
        <w-select class="w-200" :disabled="true" :options="option1" :title="data3" :value="data3">
        </w-select>
        <div class="hello-world"><em>{{data3}}</em></div>
      </div>
      <pre><code>
      </code></pre>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      data1:[''],
      data2:['',''],
      data3:[''],
      option1: [{
        value: '0',
        label: ''
      }, {
        value: '1',
        label: ''
      }, {
        value: '2',
        label: '',
        disabled:true
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }],
      option2: [{
        value: '0',
        label: '',
        disabled:true
      }, {
        value: '1',
        label: ''
      }, {
        value: '2',
        label: '',
      }, {
        value: '3',
        label: '',
      }, {
        value: '4',
        label: ''
      }],
    };
  },
  methods:{
    onChange1(label, value){
      console.log(label)
      console.log(value)
      this.data1 = label;
    },
    onChange2(label, value){
      console.log(label)
      console.log(value)
      this.data2 = label;
    }
  }
}
</script>


