<template>
  <div class="home">
    <div class="banner">
      <transition-group tag="ul" name="list">
        <li v-for="(list,index) in slideList" :key="index" v-show="index===currentIndex" @mouseenter="stop" @mouseleave="go">
          <a :href="list.clickUrl" >
            <img :src="list.image" :alt="list.desc">
          </a>
        </li>
      </transition-group>
      <div class="banner-items">
        <span @mouseenter="stop" @mouseleave="go" v-for="(item,index) in slideList.length" :class="{'active':index===currentIndex}" @mouseover="change(index)"></span>
      </div>
    </div>
    <div class="wrap clear">
      <dl class="cards">
        <dt></dt>
        <dd><p></p></dd>
        <dd class="link"><router-link to="/guide"></router-link></dd>
      </dl>
      <dl class="cards">
        <dt></dt>
        <dd><p> Demo </p></dd>
        <dd class="link"><router-link to="/component"></router-link></dd>
      </dl>
      <dl class="cards">
        <dt></dt>
        <dd><p></p></dd>
        <dd class="link"><router-link to="/resource"></router-link></dd>
      </dl>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      slideList: [
        {
          "clickUrl": "#",
          "desc": "123",
          "image": "http://temp.im/1920x600/20A0FF/fff"
        },
        {
          "clickUrl": "#",
          "desc": "234",
          "image": "http://temp.im/1920x600/13ce66/fff"
        },
        {
          "clickUrl": "#",
          "desc": "345",
          "image": "http://temp.im/1920x600/ff4949/fff"
        }
      ],
      currentIndex: 0,
      timer: ''
    };
  },
  methods: {
      go() {
          this.timer = setInterval(() => {
              this.autoPlay()
          }, 3000)
      },
      stop() {
          clearInterval(this.timer)
          this.timer = null
      },
      change(index) {
          this.currentIndex = index
      },
      autoPlay() {
          this.currentIndex++
          if (this.currentIndex > this.slideList.length - 1) {
              this.currentIndex = 0
          }
      }
  },
  created() {
      this.$nextTick(() => {
          this.timer = setInterval(() => {
              this.autoPlay()
          }, 3000)
      })
  }
}
</script>
<style lang="scss">
  .clear{
    *zoom: 1;
    &:before,
    &:after {
      content: "";
      display: table;
      height: 0;
    }
    &:after {
      clear: both;
    }
  }
</style>


